import React, { Component } from 'react'
import {Container, Row, Col} from 'react-bootstrap';
import { Link } from "react-router-dom";



class contact extends Component {

  
    render() {




        return (
<div className="start-section bg-light">
<Container>
    <Row>
      <Col md={12} lg={8} xl={8}>
         <p className="h2 text-black font-weight-bold pb-2 text-lg-left text-center">Ready to get started?</p>
          <p className="text-muted h5 font-weight-normal text-lg-left text-center pb-2 ">We know trying a new service can be scary, that's why we want to make this 100% risk-free for you and your company</p>
      </Col>
      <Col md={12} lg={4} xl={4}>
        <center>

         <Link className="btn-lg btn-md btn btn-primary rounded text-white font-weight-bold py-2 px-4 rounded-lg my-4" to={'/Contact'}>Inquire Now</Link>

        </center>
      </Col>
    </Row>
    </Container>
     </div>
        )
    }
}

export default contact;